import styles from './ContainerSelector.module.css'
import './ContainerSelector.css'


const squareCounter=(number)=>{
    const square=[]
    for (let index = 0; index < number; index++) {
        square.push(index)
    }

    return(square)
}


export default function ContainerSelector(props){
    const { cubes, idKey } = props


    return (
        <div in={cubes} className={styles.containerSelector} id={`containerCube${idKey}`}>
            {
                squareCounter(cubes).map((index)=>{
                    return <div key={index} className={`square${cubes}`} />
                })
            }
        </div>
    )
}