// Funcion general que realiza la ejecucion de todo
export function updateGameStatus(index, lastImage, setLastImage){
    
    const cube= document.querySelector(`#cube${index}`)
    
    const cubeStyles = cube.classList
    //Comprueba si ha hecho match para no hacer le  efecto de nuevo
    cubeStyles[2]!==('matchPair')&&cube.classList.add('flipEffect')

    const image=cube.lastElementChild.firstElementChild
    checkImage(image.src, cube, lastImage, setLastImage, image )

}   

//Esta funcion se encarga de que no se repita la imagen cuando el user da click, entre otras cosas.
function checkImage(source, cube, lastImage, setLastImage, image){

    //Comprobacin de que si el cubo est disabled, entonces no lo carga como una imagen, permitiendo que el juego no haga un reset
    (!lastImage&&!cube.disabled) && setLastImage({source:source, cube:cube})

    //si la ultima imagen no existe o si se clickea la misma imagen que la anterior no avanza
    if(!lastImage || cube===lastImage.cube) return
    
    //Comprobacin: la imagen seleccionada fue seleccionada o si la ultima imagen fue seleccionada, entonces no se ejecuta el reset de los apres
    if(!image.parentNode.parentNode.disabled && !lastImage.cube.disabled) gameUpdater(setLastImage)

    updateImageInDisplay(lastImage, source, cube)
}

//Update del fondo
function gameUpdater(setLastImage){
    //Reinicia la ultima imagen a undefined para poder analizar el siguiente par a elegir
    setLastImage()
}

//Efectos del display
function updateImageInDisplay(lastImage, source, cube){
    const status = source === lastImage.source //Retorna si coinciden o no las imagenes

    const cubeBefore = lastImage.cube

    if(!status){
        setTimeout(()=>{
            //si alguno de los que selecciono ya estaba deshabilitado no accede a hacer la muestra de display
            if( !cube.disabled && !cubeBefore.disabled){
                cube.classList.remove('flipEffect')
                cubeBefore.classList.remove('flipEffect')

                cubeBefore.classList.add('hiddeEffect')
                cube.classList.add('hiddeEffect')
            }
            

        }, 1000)
        return
    }else{
        cubeBefore.setAttribute('disabled', '')
        cubeBefore.classList.add('matchPair')
        cube.setAttribute('disabled', '')
        cube.classList.add('matchPair')
   }

}

//Se fija si el juego termino
export function checkEndGame(count){

    const cubes = document.querySelectorAll('.cube')
    const disabledCubes= []

    for (const cube of cubes){
        cube.disabled&&disabledCubes.push(cube)
    }

    if(disabledCubes.length===parseInt(count)){
        const winDisplay = document.querySelector('#popUpWin')
        winDisplay.classList.add('intro')
        winDisplay.style.display='flex'

    }

}