import displayCategorie from "../utils/displayCategorie"
import styles from './ContainerCube.module.css'
import './animationsContainerCube.css'
import { useEffect, useState } from "react"

import { updateGameStatus, checkEndGame } from "../utils/gameFunctions"
import WinDisplay from "../WinDisplay/WinDisplay"


export default function ContainerCube(props){

    const {type, count} = props

    const [images, setImages] = useState([])
    useEffect(()=>{
        setImages(displayCategorie(type, count)) // retorna el array con los elementos a mostrar
    }, [count, type])

    const [lastImage, setLastImage]= useState()

    return(
        <>
            
            <div className={styles.container}>
                {images.map((image, index)=>{
                    return (
                        <button className={`${styles.containerCube} type${count} cube`} key={index} id={`cube${index}`} onClick={()=>{updateGameStatus(index, lastImage, setLastImage); checkEndGame(count) }}>
                            
                            <div className={styles.front}></div>

                            <div className={`${styles.back} type${count}`}>
                                <img className={`${styles.cubeImage} images`} key={index} src={image.link} alt="" />
                            </div>
                            
                        </button>
                    )
                })}
            </div>
            <WinDisplay setImagesParameters={{setImages, type, count}} />
        </>
    )
}
