import styles from './Categorie.module.css'
import { Link } from 'react-router-dom'


const typeCategory=['Animals', 'Objects', 'Mixed']
export default function Categorie(props){
    const { visible, close, cubeCount } = props

    return (
        <div style={{visibility:`${visible}`}} className={styles.container}>
            
            <div className={styles.containerButton}>
                <img className={styles.closeButton}  onClick={close} src='https://cdn.iconfinder.com/stored_data/950591/128/png?token=1680577652-wDnccGfF%2F2vjDOCQbJW9MbQmSvxqsJQjVWGwNYY6%2Bsg%3D' alt="" />
            </div>  

            <h2>{cubeCount} cubes selected</h2>

            <h3>Chosee category</h3>

            <div className={styles.containerButtons}>
               {
                typeCategory.map((type)=>{
                    return(
                        //Se divide por dos porque solo es necesario 2 imagenes y estas se pasaran como prop en el useState del game para saber cuantas imagenes renderizar
                        <Link key={type} to={`/${cubeCount}/${type}`} className={styles.categorieButton}>{type}</Link>
                    )
                })
                }
            </div>
        </div>
    )
}