// Esta funcion se encarga de saber cuantas imagenes y de que tipo mostrar (Categoria seleccionada)\
//TODO: que mixtee mas la categoria mixed, que son todos animales, y que haga un sort para mezclarlos antes de enviarlos
export default function displayCategorie(type, count){
    const toDisplay = []
    categories.forEach((element)=>{  
        element.categorie.forEach((typeElement) => {
            typeElement===type&&toDisplay.push(element)
        })
    })

    resetWinDisplayEffect()
    return randomizerArray(toDisplay, count)
}

const randomizerArray = (array, count) => {
    const random= array.sort(()=> Math.random()-0.5)

    const sliced=random.slice(0, count/2)
    
    //lo duplica con los datos anteriores para poder hacer el par del juego
    const sorted= sliced.concat(sliced)
    //Lo ordena al azar
    const toReturn=sorted.sort(()=>Math.random() - 0.5)

    return toReturn
}

const resetWinDisplayEffect=()=>{
    const winDisplay = document.querySelector('#popUpWin')
    winDisplay.style.display='none'
}