import Scene from "./Scene/Scene"
import styles from './Layers.module.css'


export default function Layers(){
    return (
        <Scene>
            <img className={styles.firstLayer}src='https://i.imgur.com/OGGBV7C.png' alt="" />
            <img className={styles.secondLayer}  data-depth-y='0.23' src="https://i.imgur.com/Ir1FKsr.png" alt="" />
            <img className={styles.thirdLayer}  data-depth-y='0.3' src="https://i.imgur.com/3Sy325D.png" alt="" />
          
            <img className={styles.sixLayer} data-depth-y='0.4' src="https://i.imgur.com/Jgpmpil.png" alt="" />

              <img className={styles.fourLayer} data-depth-x='0.2' src="https://i.imgur.com/qX0mm9a.png" alt="" />
            <img className={styles.fiveLayer} data-depth-x='0.5' src="https://i.imgur.com/kM0CzFx.png" alt="" />
        </Scene>
    )
}