import styles from './Game.module.css'
import { useParams } from "react-router-dom";

import ContainerCube from './Components/ContainerCube/ContainerCube';
import { Link } from 'react-router-dom';

export default function Game (){
    const {count, type} = useParams()

    //count: nos dice cuantos cubos selecciono el usuario, type: nos dice que categoria selecciono el usuario
    return (
        <div className={styles.layoutGame}>
            <Link to={'/'} className={styles.return}>
                <img src="https://cdn.iconfinder.com/stored_data/960236/128/png?token=1680580518-7GPTqbKwr7M4DFb2cPTr5TY9SXlXAHkchjixejA2jts%3D" alt="" />
                <span>Return</span>    
            </Link>
            <ContainerCube count={count} type={type} />
        </div>
    )
}

