import styles from './WinDisplay.module.css'
import './WinAnimations.css'
import { Link } from 'react-router-dom'
import displayCategorie from '../utils/displayCategorie'

export default function WinDisplay(props){

    const { setImagesParameters } = props
    const {setImages, type, count} = setImagesParameters
    const clickHandlerReset = () =>{
        const cubes = document.querySelectorAll('.cube')
        for( const cube of cubes){
            //remueve para que pueda volver a ser utilizada
            cube.classList.remove('matchPair')
            cube.classList.remove('flipEffect')
            
            //reincia para que haga el efecto de volverse a ocultar si gan el juego
            cube.classList.remove('hiddenEffect')
            cube.classList.add('hiddenEffect')
            //las habilita
            cube.removeAttribute('disabled')
        }
        setTimeout(()=>{
            setImages(displayCategorie(type, count))
        }, 500)
    }

    return (

        <div aria-haspopup className={styles.container} id='popUpWin'>
            <h1>You win!</h1>

            <div>
                <p>What do you prefer?</p>

                <div className={styles.containerButtons}>
                    <div onClick={clickHandlerReset}>
                        <img src="https://cdn.iconfinder.com/stored_data/955749/128/png?token=1680577585-EzX4XkPUfLeaW%2FmyqU4SvxRgrujKdz1NbQyjBv7pCUI%3D" alt=""/>
                    </div>
                    <Link to='/' className={styles.containerHome}>
                        <img src="https://cdn.iconfinder.com/stored_data/955750/128/png?token=1680577600-bjZFuVbI4r1eG2%2BrmQ0Gh3RzpFCrJbcuMZTWmQk5Nkk%3D" alt="" />
                    </Link>
                </div>
            </div>

        </div>
    )
}