import styles from './Home.module.css'
import ContainerSelector from './Components/ContainerSelector/ContainerSelector'

import Categorie from './Components/ContainerSelector/Categorie/Categorie'
import { useEffect, useState } from 'react'


import Layers from '../../../Components/Scene/Layers'

//Dice cuantos cubitos queres dentro del contenedor y su valor
const cubes=[4,12,16]
export default function Home(){
        //Selector game
    const [cubeSelected, setCubeSelected] = useState(1)
    function clickHanlder(buttonType){
        
        buttonType==='rightBtn'?(
            cubeSelected===(cubes.length-1)?setCubeSelected(cubeSelected-(cubes.length-1)):setCubeSelected(cubeSelected+1)
        )
        :
        (
            cubeSelected===(0)?setCubeSelected(cubeSelected+(cubes.length-1)):setCubeSelected(cubeSelected-1)
        )

    }
    useEffect(()=>{
        for (let index = 0; index < (cubes.length); index++) {
            const element = document.querySelector(`#containerCube${index}`);
            if(element.id.includes(cubeSelected)){
                element.style=`box-shadow: 2px 2px 10px 0px rgba(0,0,0,0.75);`
                element.style.transform='scale(1.1)'
                element.style.opacity='1'
                element.style.animation='scaleTransitionBig 0.3s ease-in 1'
               
            }else{
                element.style=`box-shadow: none;`
                element.style.transform='scale(1)'
                element.style.opacity='0.7'
                element.style.animation='scaleTransitionLow 0.3s ease-in 1'
            }
        }
    }, [cubeSelected])

    //pop-up
    const [showType, setType] = useState({visibility:'hidden',cubeCount:undefined })
    const closeCategories=()=>{
        setType({visibility:'hidden'})
    }
    function startHandler(){
        const startChoice = document.querySelector(`#containerCube${cubeSelected}`)
        const cubes=(startChoice.attributes.in.value)
        setType({visibility:'visible', cubeCount: cubes})
    }

    return(
        <>
            {/* //TODO:MAKE COMPONENTS */}
                <div className={styles.containerGame}>

                    <div className={styles.containerDisplaySelectCube}>
                        <img src='https://cdn.iconfinder.com/stored_data/964548/128/png?token=1680734505-baBWm7UfmwgwViTnqp9LCuZZTYV8ZPttb6R80h4Q6fk%3D' className={styles.triangleLeft} onClick={()=>clickHanlder('leftBtn')} alt='left'/>

                        <div className={styles.containerCubes}>
                                {
                                    cubes.map((number, index)=>{
                                        return <ContainerSelector key={index} idKey={index} cubes={number} primarySelected={cubeSelected===index&&'active'} />
                                    })  
                                }
                        </div>

                        <img src='https://cdn.iconfinder.com/stored_data/964548/128/png?token=1680734505-baBWm7UfmwgwViTnqp9LCuZZTYV8ZPttb6R80h4Q6fk%3D' className={styles.triangleRight} onClick={()=>clickHanlder('rightBtn')} alt='right' />
                    </div>

                    <button className={styles.buttonStart} onClick={startHandler}>START</button>
                </div>
                <Categorie visible={showType.visibility} close={()=>closeCategories()} cubeCount={showType.cubeCount} />
                                
        </>
    )
}
