import { useState } from "react"

import styles from './AudioControls.module.css'
import Audio from "./Components/Audio"

export default function AudioControls(){
    const [sound, setSound] = useState(false)

    function clickHandler(){
        const audio = document.querySelector('audio')

        if(sound){ audio.pause(); setSound(false); return } 

        audio.play();
        setSound(true)

    }

    return(
        <div style={{position:'absolute', left:'5vw', display:'flex'}}>
            <Audio/>

            <div className={styles.containerButtons} onClick={clickHandler}> 

                {
                    sound?(
                        <img src="https://cdn.iconfinder.com/stored_data/955454/128/png?token=1680577736-eAolnbAWF2PzHLE7ZvxBPPECyWaf%2Bvh4HSY5ytFVrro%3D" alt="imagenButtons" />
                    )
                        :
                    (
                        <img src="https://cdn.iconfinder.com/stored_data/955452/128/png?token=1680577779-EPhgG6VMIO9MRB9JZT23R08sCoyw8fimHCjCYhyYDFc%3D" alt="imagenButtons" />
                    )
                }
            </div>

        </div>
    )
}